import React from 'react';

import './index.scss';

interface IProps {
  logoSrc: string;
  mode: 'floors' | 'units' | 'amenities';
  onClose: () => void;
  fontSize: number;
}

type Props = IProps;

const HelpPopup: React.SFC<Props> = ({ logoSrc, mode, fontSize, onClose }) => {
  const welcomeText = {
    floors: 'unit browser',
    units: '3D unit search',
    amenities: '3D amenities browser',
  }[mode];

  const helpText = {
    floors: [
      `Please use the blue up and down arrows in the top left corner to
    navigate between floors of the building.`,
    ],
    units: [
      `Click and drag anywhere on the screen to rotate the building.`,
      `To see more information about an available unit, just click on it!`,
    ],
    amenities: [
      `Please drag your cursor to spin the building. Click on one of the amenity options listed below to see the amenity up close!`,
    ],
  }[mode];

  return (
    <div className="ev-help-popup-bg">
      <div className="ev-help-popup" style={{ fontSize: `${fontSize}px` }}>
        <button onClick={onClose} className="close-btn">
          <img src={B64_CLOSE} alt="Close" />
        </button>
        <img className="ev-brand-logo" src={logoSrc} />
        <hr className="ev-hr" />
        <p className="ev-help-welcome">Welcome to our {welcomeText}</p>
        {helpText.map((txt) => (
          <p className="ev-help-text">{txt}</p>
        ))}
        <button onClick={onClose} className="continue-btn">
          Continue
        </button>
      </div>
    </div>
  );
};

export const B64_CLOSE = `data:image/png;base64,
`;
export default HelpPopup;
