import React, { useEffect, useRef, useState } from 'react';
import './index.scss';
import { IUnitOverlayFields } from 'src';

interface IProps {
  name: string;
  bedrooms: number;
  bathrooms: number;
  interiorSquareFeet: number;
  exteriorSquareFeet: number;
  available: boolean;
  floorplanSrc: string;
  onClose: () => void;
  onExpandImage: (src: string) => void;
  price: number;
  unitOverlayFields: IUnitOverlayFields;
}

type Props = IProps;

const CLOSE_BUTTON_B64 = `data:image/png;base64,`;

const UnitOverlay: React.SFC<Props> = ({
  name,
  bedrooms,
  bathrooms,
  interiorSquareFeet,
  exteriorSquareFeet,
  available,
  onClose,
  floorplanSrc,
  onExpandImage,
  price,
  unitOverlayFields,
}) => {
  return (
    <div className={`ev-unit-overlay-container`}>
      <div className="ev-gallery-container">
        <img src={floorplanSrc} onClick={() => onExpandImage(floorplanSrc)} />
        <button onClick={() => onExpandImage(floorplanSrc)}>
          <img src={B64_FULLSCREEN} alt="Fullscreen" />
          <span>Full Screen</span>
        </button>
      </div>
      <div className="ev-unit-detail-container">
        <h1 className="ev-unit-detail-name">{name}</h1>
        {!unitOverlayFields.disableBedrooms && (
          <div className="ev-unit-detail">
            <div>Bedrooms</div>
            <span>{bedrooms}</span>
          </div>
        )}
        {!unitOverlayFields.disableBathrooms && (
          <div className="ev-unit-detail">
            <div>Bathrooms</div>
            <span>{bathrooms}</span>
          </div>
        )}
        {!unitOverlayFields.disableInteriorSize && (
          <div className="ev-unit-detail">
            <div>Interior Size</div>
            <span>{interiorSquareFeet} sq ft</span>
          </div>
        )}

        {!unitOverlayFields.disableExteriorSize && (
          <div className="ev-unit-detail">
            <div>Exterior Size</div>
            <span>{exteriorSquareFeet} sq ft</span>
          </div>
        )}

        {!unitOverlayFields.disableAvailability && (
          <div className="ev-unit-detail">
            <div>Availability</div>
            <span>{available ? 'Available Now' : 'Unavailable'}</span>
          </div>
        )}
        {!unitOverlayFields.disablePricing && (
          <div className="ev-unit-detail">
            <div>Pricing</div>
            <span>{price && price > 0 ? `$${price}` : 'Call for Pricing'}</span>
          </div>
        )}
      </div>
      <button className="ev-close-unit-overlay" onClick={onClose}>
        <img src={CLOSE_BUTTON_B64} />
      </button>
    </div>
  );
};

export const B64_FULLSCREEN = `data:image/png;base64,
`;

export default UnitOverlay;
