import React, { useState } from 'react';
import './index.scss';
import { IUnitOverlayFields } from 'src';
import { B64_CLOSE } from '../HelpPopup';

interface IProps {
  name: string;
  bedrooms: number;
  bathrooms: number;
  interiorSquareFeet: number;
  exteriorSquareFeet: number;
  available: boolean;
  floorplanSrc: string;
  onClose: () => void;
  onExpandImage: (src: string) => void;
  price: number;
  unitOverlayFields: IUnitOverlayFields;
  visible: boolean;
}

type Props = IProps;

const UnitOverlayPortrait = (props: Props) => {
  const {
    name,
    bedrooms,
    bathrooms,
    interiorSquareFeet,
    exteriorSquareFeet,
    available,
    floorplanSrc,
    onClose,
    onExpandImage,
    price,
    unitOverlayFields,
    visible,
  } = props;

  return (
    <div
      className="ev-unit-overlay-portrait-container"
      style={{
        right: visible ? '0' : '-94%',
        visibility: visible ? 'visible' : 'hidden',
      }}>
      <button onClick={onClose}>
        <img src={B64_CLOSE} alt="Close" />
      </button>
      <div className="ev-portrait-unit-gallery-container">
        <img
          src={floorplanSrc}
          alt="Unit Floorplan"
          onClick={() => onExpandImage(floorplanSrc)}
        />
      </div>
      <div className="ev-portrait-unit-details-container">
        <h2 className="ev-portrait-unit-name">{name}</h2>

        {!unitOverlayFields.disableBedrooms && (
          <div className="ev-portrait-unit-detail">
            <div>Bedrooms</div>
            <span>{bedrooms}</span>
          </div>
        )}

        {!unitOverlayFields.disableBathrooms && (
          <div className="ev-portrait-unit-detail">
            <div>Bathrooms</div>
            <span>{bathrooms}</span>
          </div>
        )}

        {!unitOverlayFields.disableInteriorSize && (
          <div className="ev-portrait-unit-detail">
            <div>Interior Size</div>
            <span>{interiorSquareFeet} sq ft</span>
          </div>
        )}
        {!unitOverlayFields.disableExteriorSize && (
          <div className="ev-portrait-unit-detail">
            <div>Exterior Size</div>
            <span>{exteriorSquareFeet} sq ft</span>
          </div>
        )}

        {!unitOverlayFields.disableAvailability && (
          <div className="ev-portrait-unit-detail">
            <div>Availability</div>
            <span>{available ? 'Available Now' : 'Unavailable'}</span>
          </div>
        )}

        {!unitOverlayFields.disablePricing && (
          <div className="ev-portrait-unit-detail">
            <div>Pricing</div>
            <span>{price && price > 0 ? `$${price}` : 'Call for Pricing'}</span>
          </div>
        )}
      </div>
    </div>
  );
};

export default UnitOverlayPortrait;
