import React, { useState, useEffect } from 'react';
import './index.scss';
import ActionBar from './ActionBar';
import ActionBarPortrait from './ActionBarPortrait';
import FloorSwitcher from './FloorSwitcher';
import Tutorial from './Tutorial';
import UnitPopup from './UnitPopup';
import Gallery from './Gallery';
import UnitOverlay from './UnitOverlay';
import UnitOverlayExpansion from './UnitOverlayExpansion';
import HelpPopup from './HelpPopup';
import UnitOverlayPortrait from './UnitOverlayPortrait';
import Loader from 'react-loader-spinner';
import 'react-loader-spinner/dist/loader/css/react-spinner-loader.css';

import axios, { AxiosResponse } from 'axios';

import {
  IUnit,
  OrbitReelOptions,
  orbitReelCreate,
  orbitReelInitialize,
  orbitReelStart,
  orbitReelSetLabelsVisible,
  orbitReelUpdateShownUnits,
  orbitReelActivateTransition,
  OrbitReelState,
  orbitReelSetFloorsVisible,
  setCurrentOrbitIndex,
  OrbitReelControlMode,
  orbitReelSetControlMode,
} from '../orbit-reel';
import { IOrbitReelOptions, ICustomOptions, ITranslations } from 'src';
import isMobile from 'is-mobile';

export interface ITransition {
  label: string;
  name: string;
  text: string[];
  gallery: string[];
}

interface IProps {
  token: string;
  externalOptions: IOrbitReelOptions;
  customOptions: ICustomOptions;
}

type Props = IProps;

const mobile = isMobile();

const fetchFromStore = async (url: string): Promise<AxiosResponse> => {
  try {
    const response = await axios.get(url);

    return response;
  } catch (e) {
    return undefined;
  }
};

export const TranslationsContext = React.createContext({});

const App: React.SFC<Props> = ({ token, externalOptions, customOptions }) => {
  const [currentMode, setCurrentMode] = useState<
    'units' | 'floors' | 'amenities'
  >('units');

  const [visible, setVisible] = useState<boolean>(true);
  const [activeTransition, setActiveTransition] = useState<string>('');
  const [isTransitionsEnabled, setTransitionsEnabled] = useState<boolean>(true);
  const [orbitReelState, setOrbitReelState] = useState<OrbitReelState>(null);
  const [transitions, setTransitions] = useState<ITransition[]>(undefined);
  const [keyplans, setKeyplans] = useState<number[]>();
  const [floorIndex, setFloorIndex] = useState<number>(0);
  const [widthFallback, setWidthFallback] = useState<boolean>(false);
  const [mousePosition, setMousePosition] = useState<number[]>([0, 0]);
  const [flipPopupX, setFlipPopupX] = useState<boolean>(false);
  const [flipPopupY, setFlipPopupY] = useState<boolean>(false);
  const [isPopupVisible, setPopupVisible] = useState<boolean>(false);
  const [popupUnit, setPopupUnit] = useState<IUnit>(undefined);
  const [amenityMode, setAmenityMode] = useState<boolean>(false);
  const [isViewingGallery, setViewingGallery] = useState<boolean>(false);
  const [isUnitOverlayOpen, setUnitOverlayOpen] = useState<boolean>(false);
  const [unitForOverlay, setUnitForOverlay] = useState<IUnit>(undefined);
  const [isImageExpanded, setImageExpanded] = useState<boolean>(false);
  const [imageExpansionSrc, setImageExpansionSrc] = useState<string>('');
  const [fontSize, setFontSize] = useState<number>(16);
  const [dismissedHelpPopup, setDismissedHelpPopup] = useState<boolean>(false);
  const [portrait, setPortrait] = useState<boolean>(false);
  const [units, setUnits] = useState<IUnit[]>([]);
  const [controlMode, setControlMode] = useState<OrbitReelControlMode>(
    OrbitReelControlMode.ORBIT
  );
  const [isLoaded, setLoaded] = useState<boolean>(false);
  const assetStoreURL =
    (externalOptions && externalOptions.assetStoreUrl) ||
    'https://orbitreelstore.s3.amazonaws.com';

  const translations: ITranslations = Object.assign(
    {
      buildingsAmenities: 'Building & Amenities',
      viewAll: 'View All',
      unitSearch: '3D Unit Search',
    },
    customOptions ? customOptions.translations : {}
  );

  const disableUnitFilterControls =
    customOptions && customOptions.disableUnitFilterControls;
  const disableUnitOverlay = customOptions && customOptions.disableUnitOverlay;

  const labelFontSize =
    (customOptions && customOptions.labelFontSize) || '12px';

  const disableAmenitiesGallery =
    customOptions && customOptions.disableAmenitiesGallery;

  const disableHelpPopup =
    customOptions && customOptions.disableIntroductionPopup;

  const debugMode = customOptions && customOptions.debugMode;

  const initialFrameNumber =
    customOptions && customOptions.initialFrameNumber
      ? Number(customOptions.initialFrameNumber)
      : 0;

  const unitOverlayFields = customOptions
    ? Object.assign({}, customOptions.unitOverlayFields)
    : {};

  const logoSrc = customOptions && customOptions.logoSrc;
  const watermarkLogoSrc = customOptions && customOptions.watermarkLogoSrc;

  const forcedMode = customOptions ? customOptions.mode || 'all' : 'all';

  let anchorActionBarToTop =
    customOptions && customOptions.anchorActionBar === 'top';

  if (portrait) {
    anchorActionBarToTop = false;
  }

  const onThetaChange = customOptions && externalOptions.onThetaChanged;

  const containerRef = React.useRef();
  const rootRef = React.useRef();

  const handleResize = () => {
    const container: HTMLDivElement = containerRef.current;

    if (container) {
      const { clientWidth: width, clientHeight: height } = container;

      const vw = width / 100;
      const vh = height / 100;
      const vmin = Math.min(vw, vh);

      const fontSize = Math.min(16, vw + vh + 0.5 * vmin);

      setFontSize(fontSize);

      const portrait = height > width || width <= 1088;
      setPortrait(portrait);
    }
  };

  const isUnitAvailable = (unitName: string) => {
    const unit = units.find((unit) => unit.name === unitName);

    return !unit.isUnavailable;
  };

  const updateMouse = (evt: React.MouseEvent<HTMLDivElement>) => {
    const { clientX, clientY } = evt;
    const root: HTMLDivElement = rootRef.current;
    if (root) {
      const flipX = clientX >= root.clientWidth - fontSize * 15.625;
      const flipY = clientY >= root.clientHeight - fontSize * 15.625;

      setMousePosition([clientX, clientY]);
      setFlipPopupX(flipX);
      setFlipPopupY(flipY);
    }
  };

  const onLabelEnter = (unit: IUnit) => {
    setPopupUnit(unit);
    setPopupVisible(true);
  };

  const onLabelLeave = () => {
    setPopupVisible(false);
    setPopupUnit(undefined);
  };

  const openUnitOverlay = (unit: IUnit) => {
    if (!disableUnitOverlay) {
      setUnitForOverlay(unit);
      setUnitOverlayOpen(true);
    }
  };

  const closeUnitOverlay = () => {
    setUnitOverlayOpen(false);
  };

  const transitionTo = (name: string) => {
    if (isTransitionsEnabled && name !== activeTransition) {
      setTransitionsEnabled(false);
      orbitReelActivateTransition(orbitReelState, name, () =>
        setTransitionsEnabled(true)
      );

      setActiveTransition(name);
    }
  };

  const onFilterChange = (filter: (unit: IUnit) => boolean) => {
    if (orbitReelState) {
      const visibleUnits = units.filter(filter);
      orbitReelUpdateShownUnits(orbitReelState, visibleUnits);
    }
  };

  React.useEffect(() => {
    if (orbitReelState) {
      orbitReelSetControlMode(orbitReelState, controlMode);
    }
  }, [controlMode]);

  React.useEffect(() => {
    if (orbitReelState) {
      orbitReelSetLabelsVisible(orbitReelState, currentMode === 'units');
    }
  }, [currentMode]);

  React.useEffect(() => {
    if (externalOptions.units && externalOptions.units.length > 0) {
      setUnits(externalOptions.units);
    } else {
      document.addEventListener('EV_EVT--RECEIVE_UNITS', (evt: CustomEvent) => {
        setUnits(evt.detail as IUnit[]);
      });
    }
  }, []);

  const orbitImageUrls = (resolution: string = 'index') =>
    Array.from({ length: 120 }).map((_, index) => {
      return `${assetStoreURL}/${token}/360/${index * 3}/${resolution}.jpg`;
    });

  React.useEffect(() => {
    if (!units || units.length <= 0) {
      return;
    }
    window.addEventListener('resize', handleResize);

    const initialize = async () => {
      let labels = undefined;
      const labelResponse = await fetchFromStore(
        `${assetStoreURL}/${token}/360/labels.json`
      );

      if (labelResponse && labelResponse.status === 200) {
        labels = labelResponse.data.labels;
      }

      let camera = undefined;
      const cameraResponse = await fetchFromStore(
        `${assetStoreURL}/${token}/360/camera.json`
      );

      if (cameraResponse && cameraResponse.status === 200) {
        camera = cameraResponse.data.camera;
      }

      let transitions = undefined;
      const transitionResponse = await fetchFromStore(
        `${assetStoreURL}/${token}/360/transitions.json`
      );
      if (transitionResponse && transitionResponse.status === 200) {
        transitions = transitionResponse.data.transitions;
      }

      let keyplans = undefined;
      const keyplansResponse = await fetchFromStore(
        `${assetStoreURL}/${token}/360/keyplans.json`
      );

      if (keyplansResponse && keyplansResponse.status === 200) {
        keyplans = keyplansResponse.data.keyplans;
      }

      let occlusion = undefined;
      const occlusionResponse = await fetchFromStore(
        `${assetStoreURL}/${token}/360/occlusion.json`
      );

      if (occlusionResponse && occlusionResponse.status === 200) {
        occlusion = occlusionResponse.data.occlusion;
      }

      const _keyplans: number[] = keyplans
        ? keyplans.map((fl) => Number(fl))
        : [];

      setKeyplans(_keyplans);
      setTransitions(transitions);

      const options: OrbitReelOptions = {
        container: rootRef.current,
        imageWidth: 1920,
        imageHeight: 1080,
        orbitImageUrls: orbitImageUrls(),
        orbitLowResImageUrls: orbitImageUrls(),
        debugMode,
        units: units,
        labels,
        camera,
        transitions,
        occlusion,
        labelColor: customOptions && customOptions.labelColor,
        labelColor1BR: customOptions && customOptions.labelColor1BR,
        labelColor2BR: customOptions && customOptions.labelColor2BR,
        labelColor3BR: customOptions && customOptions.labelColor3BR,
        labelColor4BR: customOptions && customOptions.labelColor4BR,
        hideAvailableUnits: customOptions && customOptions.hideAvailableUnits,
        hideUnavailableUnits:
          customOptions && customOptions.hideUnvailableUnits,
        amenityModeCallback: setAmenityMode,
        labelEnterCallback: (label) => {
          const unit = units.find((unit) => unit.name === label.name);

          onLabelEnter(unit);
        },
        labelLeaveCallback: (label) => {
          const unit = units.find((unit) => unit.name === label.name);

          onLabelLeave();
        },
        labelClickedCallback: (label) => {
          const unit = units.find((unit) => unit.name === label.name);

          openUnitOverlay(unit);

          if (externalOptions.onUnitClicked) {
            externalOptions.onUnitClicked(unit);
          }
        },
        amenityClickedCallback: externalOptions.onAmenityClicked,
        amenityRegionClickedCallback: externalOptions.onAmenityRegionClicked,
        floorClickedCallback: (floor) => {
          let newFloorIndex = 0;

          keyplans.forEach((keyplan, index) => {
            if (keyplan === floor) {
              newFloorIndex = index;
            }
          });

          setFloorIndex(newFloorIndex);
          setVisible(false);
        },
        thetaChangedCallback: (theta: number) => {
          if (orbitReel.currentOrbitAngle !== theta) {
            onLabelLeave();
            onThetaChange && onThetaChange(theta);
          }
        },
      };

      const orbitReel = orbitReelCreate(options);

      orbitReelInitialize(orbitReel, 0).then(() => {
        setLoaded(true);
        orbitReelStart(orbitReel);
        orbitReelSetLabelsVisible(orbitReel, true);
        setCurrentOrbitIndex(orbitReel, initialFrameNumber);
      });

      setOrbitReelState(orbitReel);

      document.addEventListener(
        'EV_EVT--OVERRIDE_FILTER',
        (evt: CustomEvent) => {
          const filter: (unit: IUnit) => boolean = evt.detail;
          const visibleUnits = units.filter(filter);
          orbitReelUpdateShownUnits(orbitReel, visibleUnits);
        }
      );

      const orbitReelReadyEvt = new CustomEvent('EV_EVT--REEL_READY');
      document.dispatchEvent(orbitReelReadyEvt);

      handleResize();
    };

    initialize();

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, [units, setLoaded]);

  const updateFloor = (delta: number) => {
    let newIndex = floorIndex + delta;
    if (newIndex < 0) {
      newIndex = 0;
    } else if (newIndex >= keyplans.length) {
      newIndex = keyplans.length - 1;
    }

    setFloorIndex(newIndex);
  };

  const handleUnitNameClicked = (unitName: string) => {
    const unit = units.find((unit) => unit.name === unitName);
    if (externalOptions.onUnitClicked) {
      externalOptions.onUnitClicked(unit);
    }

    openUnitOverlay(unit);
  };

  const openImageExpansion = (src: string) => {
    setImageExpanded(true);
    setImageExpansionSrc(src);
  };

  const closeImageExpansion = () => {
    setImageExpanded(false);
  };

  console.log('active transition', activeTransition, amenityMode);
  console.log('loaded?', isLoaded);
  const showUnitOverlayLandscape = isUnitOverlayOpen && !portrait;
  const showUnitOverlayPortrait = isUnitOverlayOpen && portrait;
  const showActionBarLandscape = !disableUnitFilterControls && !portrait;
  const showActionBarPortrait = !disableUnitFilterControls && portrait;

  const activeTransitionData = transitions
    ? transitions.find((t) => t.name === activeTransition)
    : undefined;

  const showGallery =
    activeTransitionData &&
    activeTransitionData.gallery &&
    activeTransitionData.gallery.length > 0;
  const showPopup =
    isPopupVisible && popupUnit !== null && popupUnit !== undefined && !mobile;

  const showHelpPopup = !dismissedHelpPopup && !disableHelpPopup;

  return (
    <TranslationsContext.Provider value={translations}>
      <div id="ev-or-root" ref={rootRef} onMouseMove={updateMouse}>
        <div id="ev-or-ui-root">
          <div className="or-container" ref={containerRef}>
            {!isLoaded ? (
              <div className="ev-loading-spinner">
                <Loader type="Circles" color="#000" width={100} height={100} />
              </div>
            ) : (
              <>
                <img
                  src={watermarkLogoSrc}
                  className={`ev-watermark-logo ${
                    anchorActionBarToTop && 'top'
                  }`}
                />
                {showHelpPopup && (
                  <HelpPopup
                    fontSize={fontSize}
                    logoSrc={logoSrc}
                    mode={currentMode}
                    onClose={() => setDismissedHelpPopup(true)}
                  />
                )}
                {showUnitOverlayLandscape && (
                  <UnitOverlay
                    price={unitForOverlay.price}
                    onExpandImage={openImageExpansion}
                    name={unitForOverlay.name}
                    bedrooms={unitForOverlay.bedrooms}
                    bathrooms={unitForOverlay.bathrooms}
                    available={!unitForOverlay.isUnavailable}
                    interiorSquareFeet={unitForOverlay.interiorSquareFeet}
                    exteriorSquareFeet={unitForOverlay.exteriorSquareFeet}
                    onClose={closeUnitOverlay}
                    floorplanSrc={unitForOverlay.floorplanAssetsSrc[0]}
                    unitOverlayFields={unitOverlayFields}
                  />
                )}
                <UnitOverlayPortrait
                  price={unitForOverlay && unitForOverlay.price}
                  onExpandImage={openImageExpansion}
                  visible={showUnitOverlayPortrait}
                  name={unitForOverlay && unitForOverlay.name}
                  bedrooms={unitForOverlay && unitForOverlay.bedrooms}
                  bathrooms={unitForOverlay && unitForOverlay.bathrooms}
                  available={unitForOverlay && !unitForOverlay.isUnavailable}
                  interiorSquareFeet={
                    unitForOverlay && unitForOverlay.interiorSquareFeet
                  }
                  exteriorSquareFeet={
                    unitForOverlay && unitForOverlay.exteriorSquareFeet
                  }
                  onClose={closeUnitOverlay}
                  floorplanSrc={
                    unitForOverlay && unitForOverlay.floorplanAssetsSrc[0]
                  }
                  unitOverlayFields={unitOverlayFields}
                />
                {visible ? (
                  <>
                    {isViewingGallery && showGallery ? (
                      <Gallery gallery={activeTransitionData.gallery} />
                    ) : (
                      <>
                        {showActionBarLandscape && (
                          <ActionBar
                            shouldAnchorTop={anchorActionBarToTop}
                            transitions={transitions}
                            onFilterChange={onFilterChange}
                            onTransition={transitionTo}
                            activeTransition={activeTransition}
                            onModeChange={setCurrentMode}
                          />
                        )}
                        {showActionBarPortrait && (
                          <ActionBarPortrait
                            onModeChange={setCurrentMode}
                            transitions={transitions}
                            onFilterChange={onFilterChange}
                            onTransition={transitionTo}
                            activeTransition={activeTransition}
                          />
                        )}
                      </>
                    )}

                    <Tutorial shouldAnchorTop={anchorActionBarToTop} />
                  </>
                ) : (
                  <>
                    {keyplans && keyplans.length > 0 && (
                      <FloorSwitcher
                        assetStoreUrl={assetStoreURL}
                        widthFallback={widthFallback}
                        onClose={() => setVisible(true)}
                        floor={keyplans[floorIndex]}
                        onChangeFloor={updateFloor}
                        token={token}
                        isUnitAvailable={isUnitAvailable}
                        onUnitNameClicked={handleUnitNameClicked}
                        labelEnterCallback={(unitName) => {
                          const unit = units.find(
                            (unit) => unit.name === unitName
                          );

                          onLabelEnter(unit);
                        }}
                        labelLeaveCallback={() => {
                          onLabelLeave();
                        }}
                      />
                    )}
                  </>
                )}
                {showPopup && (
                  <UnitPopup
                    fontSize={fontSize}
                    name={popupUnit.name}
                    bedrooms={popupUnit.bedrooms}
                    bathrooms={popupUnit.bathrooms}
                    squareFeet={popupUnit.exteriorSquareFeet}
                    price={popupUnit.price}
                    floorplanSrc={popupUnit.floorplanAssetsSrc[0]}
                    x={mousePosition[0]}
                    y={mousePosition[1]}
                    flipX={flipPopupX}
                    isUnavailable={popupUnit.isUnavailable}
                    flipY={flipPopupY}
                  />
                )}
                {isImageExpanded && (
                  <UnitOverlayExpansion
                    src={imageExpansionSrc}
                    onClose={closeImageExpansion}
                  />
                )}
              </>
            )}
          </div>
        </div>
      </div>
    </TranslationsContext.Provider>
  );
};

export default App;
